import React from "react";
import { Route, BrowserRouter, Switch, Redirect } from "react-router-dom";

import { RouteWithLayout } from "./components";
import {
  Main as MainLayout,
  Back as BackLayout,
  Minimal as MinimalLayout,
} from "./layouts";
import { Landing, Akun, ProdukView, NotFound } from "./views";

const Routes = () => {
  return (
    <BrowserRouter>
      <Switch>
        <Route exact path="/">
          <Redirect to="/landing" />
        </Route>
        <Route exact path="/va_factory">
          <Redirect to="/landing" />
        </Route>
        <RouteWithLayout
          component={Landing}
          exact
          layout={MainLayout}
          path="/landing"
        />
        <RouteWithLayout
          component={Akun}
          exact
          layout={MinimalLayout}
          path="/akun"
        />
        <RouteWithLayout
          component={ProdukView}
          exact
          layout={BackLayout}
          path="/produk-view"
        />
        <RouteWithLayout
          component={NotFound}
          exact
          layout={MainLayout}
          path="/not-found"
        />
        <Redirect to="/not-found" />
      </Switch>
    </BrowserRouter>
  );
};

export default Routes;
