import React from "react";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import Bossman from "assets/img/bossman.jpg";
import { Avatar, Box, IconButton } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  large: {
    width: theme.spacing(7),
    height: theme.spacing(7),
  },
}));

function Profile() {
  const classes = useStyles();
  return (
    <React.Fragment>
      <Box
        bgcolor="#ffffff"
        width="100%"
        height="80px"
        padding="0 20px"
        display="flex"
        alignItems="center"
      >
        <Avatar alt="Remy Sharp" src={Bossman} className={classes.large} />
        <Box flexGrow={1} paddingLeft="7px" maxWidth="70%" whiteSpace="nowrap">
          <Box component="div" textOverflow="ellipsis" overflow="hidden">
            Bossman Sontoloyo Mardigu Wowiek
          </Box>
          <Box
            color="#9e9e9e"
            component="div"
            textOverflow="ellipsis"
            overflow="hidden"
          >
            Bossman@gmail.comdassda asdasdasdasads
          </Box>
        </Box>
        <IconButton aria-label="tab">
          <ChevronRightIcon />
        </IconButton>
      </Box>
    </React.Fragment>
  );
}

export default Profile;
